import { useNavigate } from "react-router-dom";
import "./DropBar.css";
const DropBar = (props) => {
  const navigate = useNavigate();

  const goTo1 = () => {
    navigate("/instruction");
  };
  const goTo2 = () => {
    navigate("/instruction1");
  };
  const goTo3 = () => {
    navigate("/instruction2");
  };
  const goTo4 = () => {
    navigate("/instruction3");
  };
  const goTo5 = () => {
    navigate("/instruction4");
  };
  const goTo7 = () => {
    navigate("/instruction5");
  }

  //    React  
  const changeColor = (element) => {
    element.style.color = "#5287C6";
  }

  const restoreColor = (element) => {
    element.style.color = "";
     //       
  }

  return (
    <div className="frame1-container">
      <p className="lists" onClick={goTo1} onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}> </p>
      <p className="lists" onClick={goTo2} onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}>   </p>
      <p className="lists" onClick={goTo3} onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}>   </p>
      <p className="lists" onClick={goTo4} onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}>  </p>
      <p className="lists" onClick={goTo5} onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}>  </p>
      <p className="lists" onClick={goTo7} onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}> </p>
      <a href="https://incredible-raincoat-c52.notion.site/cdb55dd7ea5b475eaaf94ce76fa9eba4">
        <p className="lists" onMouseOver={e => changeColor(e.target)} onMouseOut={e => restoreColor(e.target)}>  </p>
      </a>
    </div>
  );
};

export default DropBar;
