import React, {useState} from 'react';
import {useEffect} from 'react';
import axios from 'axios';
import "./Home.css";
import checkbox from "../assets/checkbox.svg";
import Header from "../components/Header";
import ContactUsButton from '../components/ContactUsButton';
import checkimg from "../assets/univcheck.svg";
import icon1 from "../assets/FooterIcon1.png"
import icon2 from "../assets/FooterIcon2.png"
import icon3 from "../assets/FooterIcon3.png"
import icon4 from "../assets/FooterIcon4.png"
import icon5 from "../assets/FooterIcon5.png"
import icon6 from "../assets/FooterIcon6.png"
import icon7 from "../assets/FooterIcon7.png"
import icon8 from "../assets/FooterIcon8.png"
import footertext from "../assets/FooterText.png"
const Home = () => {
  const baseUrl = 'https://univcert.com/api';
    const [color, setcolor] = useState(false);
    const [univcheck, setunivcheck] = useState(false);
    const [email, setemail] = useState();
    const [email1, setemail1] = useState();
    const [domain, setdomain] = useState();
    const [univName, setunivName] = useState();
    const [univName1, setunivName1] = useState();
    const [status, setstatus] = useState([]);
    const [success, setsuccess] = useState([]);
    useEffect(() => {
      setstatus("200"); 
      setsuccess("true"); 
      setdomain("mail.hongik.ac.kr");
      setemail1("abc123@mail.hongik.ac.kr");
      setunivName1("");
    }, []);
    const handleChange_email = (e)=>{
        e.preventDefault();
        setemail(e.target.value);
    }
    const handleTest = (e)=>{
      alert("    .");
  }
    const handleChange_univName = (e)=>{
        e.preventDefault();
        setunivName(e.target.value);
        setunivcheck(false);
    }
    const handleCheckUniv = async (e) => {
      e.preventDefault();
      axios.defaults.withCredentials = true;
      await axios
          .post(baseUrl + "/checkuniv", {
            univName:univName,
          },
          {
              withCredentials: true //  cors  
            })
          .then(response=>{
            console.log(response);
            if (response.data.code == null) {          
              alert("  !"); 
              setunivcheck(true);   
            }
            else if (response.data.code != null) {
              alert((response.data.message));
            }
          },
          (error)=>{
              console.log(error); 
            if (error.code != null) {
                  alert(error.message);
              }  
          });
        }

    const handleSubmit = async (e) => {
        e.preventDefault();
        axios.defaults.withCredentials = true;
        await axios
            .post(baseUrl + "/try", {
                email:email,
                univName:univName,
            },
            {
                withCredentials: true //  cors  
              })
            .then(response=>{
                console.log(response);   
                setstatus(response.data.status); 
                setdomain(response.data.domain);
                setemail1(email);
                setunivName1(univName);
                var message = response.data.message;
                if (response.data.status === 200) {              
                  setsuccess("true");
                  setcolor(false);
                  message = "success";
                }
                else if (response.data.status === 400) {
                  setsuccess("false");
                  setcolor(true);
                  message = " ,    : "+ response.data.expected;
                }
                else if (response.data.code != null) {
                  setsuccess("false");
                  setcolor(true);
                }
                setTimeout(function(){
                    alert(message);
                }, 200);
            },
            (error)=>{
                console.log(error); 
              if (error.code != null) {
                    alert("  .  : abc123@snu.ac.kr");
                    setcolor(true);
                }  
            });
    }
  
  return (
    <div className="slide-169-1">
      <div className="width-holder1">
        <Header/>
      </div>
      <div className="rectangle-1">
        <div className="width-holder2">
          <div className="flex-container">
            <span className="email-validation-and">
            <h1 className='titletext'><small className='plustext'></small> (<small className='plustext'>+</small><small className='plustext'></small>)  API</h1>
            </span>
            <span className="improve-your-deliver">
            <h1 className='oneline'>  <span className="hljs-pink"> </span>  .</h1>
            <h2 className='secondline'>      .</h2>
            </span>

            {univcheck? 
            <div className="univName-container">
              <input type="text" className="univName-input" required={true} value={univName} onChange={handleChange_univName} placeholder=" ex) "/>
              <button type="submit" className="test-button" onClick={handleCheckUniv}><img className='check' alt='check' src={checkimg}/></button>
            </div> 
            :
            <div className="univName-container">
              <input type="text" className="univName-input" required={true} value={univName} onChange={handleChange_univName} placeholder=" ex) "/>
              <button type="submit" className="test-button" onClick={handleCheckUniv}><span className="hljs-white"></span></button>
            </div> 
            }

            {univcheck? 
            <div className="email-container">
              <input type="email" className="email-input" required={true} value={email} onChange={handleChange_email} placeholder=" ex) abc123@mail.hongik.ac.kr"/>
              <button type="submit" className="test-button" onClick={handleSubmit}><span className="hljs-white"></span></button>
            </div> 
            :
            <div className="email-container">
              <input type="email" className="email-input" required={true} value={email} onChange={handleChange_email} placeholder=" ex) abc123@mail.hongik.ac.kr"/>
              <button type="submit" className="test-button" onClick={handleTest}><span className="hljs-white"></span></button>
            </div> 
            }
            
            <div className="checkbox-container">
              <img src={checkbox} alt='checkimg' className="checkbox"/>
              <ul class="checkbox-content">
                <p className='check-text'>   <br/>APIs  .</p>
              </ul>
            </div>
          </div>
          <div className="flex-container-div"></div>
          <div className="flex-container-1">
            <div className="w-full lg:w-1/3">
              <div className="info-container">
                &nbsp;UnivCert.certify("private Key", {email1}, {univName1}, true);
              </div>
              <div className="code-container">
                <pre className="flex items-center h-64 rounded shadow-xl ">                        
                  {color?
                    <code className="language-json hljs">
                      <span className="hljs-punctuation">&#123; </span><br/>
                          <span className="hljs-red">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"status"</span> <span className="hljs-punctuation">:</span> <span className="hljs-number">{status}</span><span className="hljs-punctuation">,</span><br/>
                          <span className="hljs-red">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"domain"</span> <span className="hljs-punctuation">:</span> <span className="hljs-string">{domain}</span><span className="hljs-punctuation">,</span> <br/>
                          <span className="hljs-red">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"univ_email"</span> <span className="hljs-punctuation">:</span> <span className="hljs-keyword">{email1}</span><span className="hljs-punctuation">,</span><br/>
                          <span className="hljs-red">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"univ_name"</span> <span className="hljs-punctuation">:</span> <span className="hljs-keyword">{univName1}</span><span className="hljs-punctuation">,</span><br/>
                          <span className="hljs-red">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"success"</span> <span className="hljs-punctuation">:</span> <span className="hljs-purple">{success}</span><br/>
                      <span className="hljs-punctuation">&#125; </span>
                    </code>
                    :
                    <code className="language-json hljs">
                      <span className="hljs-punctuation">&#123; </span><br/>
                          <span className="hljs-green">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"status"</span> <span className="hljs-punctuation">:</span> <span className="hljs-number">{status}</span><span className="hljs-punctuation">,</span><br/>
                          <span className="hljs-green">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"domain"</span> <span className="hljs-punctuation">:</span> <span className="hljs-string">{domain}</span><span className="hljs-punctuation">,</span> <br/>
                          <span className="hljs-green">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"univ_email"</span> <span className="hljs-punctuation">:</span> <span className="hljs-keyword">{email1}</span><span className="hljs-punctuation">,</span><br/>
                          <span className="hljs-green">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"univ_name"</span> <span className="hljs-punctuation">:</span> <span className="hljs-keyword">{univName1}</span><span className="hljs-punctuation">,</span><br/>
                          <span className="hljs-green">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"success"</span> <span className="hljs-punctuation">:</span> <span className="hljs-purple">{success}</span><br/>
                      <span className="hljs-punctuation">&#125; </span>
                    </code>
                  }
                </pre>
              </div>
              <div className="info-example">
                <span className="hljs-info">*     .  API  APIs  </span>
              </div>
            </div>        
          </div>
        </div>
      </div>
      <div className="width-holder1">
        <div className="flex-container-2">
          <div className='flex-container-3'>
            <div className='icontext'><img className='info-icon' alt="" src={icon1}/><p className="p-text">  <br/>  </p></div>
            <div className='icontext'><img className='info-icon' alt="" src={icon2}/><p className="p-text"><br/>  API</p></div>
            <div className='icontext'><img className='info-icon' alt="" src={icon3}/><p className="p-text"> <br/>  </p></div>
            <div className='icontext'><img className='info-icon' alt="" src={icon4}/><p className="p-text"> <br/> </p></div>
            <div className='icontext'><img className='info-icon' alt="" src={icon5}/><p className="p-text"><br/>   API</p></div>
          </div>
          <div className='flex-container-3'>
            <div className='icontext2'>
              <img className='info-icon2' alt="" src={icon6}/>
              <div>
                <h3>   .</h3>
                <p className="p-text">     </p>
                <p className="p-text"> ,      </p>
                <p className="p-text">     .</p>
                <h4>     .</h4>
              </div>
            </div>
            <div className='icontext2'>
              <img className='info-icon3' alt="" src={icon7}/>
              <div>
                <h3>  </h3>
                <p className="p-text">   Rest API   </p>
                <p className="p-text">       .</p>
                <p className="p-text">     </p>
                <h4>     .</h4>
              </div>
            </div>
            <div className='icontext2'>
              <img className='info-icon4' alt="" src={icon8}/>
              <div>
                <h3>  ?</h3>
                <p className="p-text">       .</p>
                <p className="p-text">univ_check  false    </p>
                <p className="p-text">   .</p>
                <h4>    .</h4>
              </div>
            </div> 
          </div>
          <img className='footer-icon' alt="" src={footertext}/>
        </div>
      </div>
      <ContactUsButton/>
    </div>
  );
};
export default Home;
